const MovableView = {
  props: {
    /** ,allverticalhorizontalnone */
    direction: {
      type: String,
      default: 'none'
    },
    /**  */
    inertia: {
      type: Boolean,
      default: false
    },
    /** movable-view */
    outOfBounds: {
      type: Boolean,
      default: false
    },
    /** xxx */
    x: {
      type: Number,
    },
    /** yyy */
    y: {
      type: Number,
    },
    /** xy */
    damping: {
      type: Number,
      default: 20
    },
    /** 0 */
    friction: {
      type: Number,
      default: 2
    },
    /**  */
    disabled: {
      type: Boolean,
      default: false
    },
    /** movable-view */
    scale: {
      type: Boolean,
      default: false
    },
    /**  */
    scaleMin: {
      type: Number,
      default: 0.5
    },
    /**  */
    scaleMax: {
      type: Number,
      default: 10
    },
    /**  0.5 - 10 */
    scaleValue: {
      type: Number,
      default: 1
    },
    /**  */
    animation: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      mX: null,                 // absolute left
      mY: null,// absolute top
      view: null,               // movable-view
      areaView: null,           //  movable-area
      startX: null, // X
      startY: null, // Y
      boxX: null, // movable-viewX
      boxY: null, // movable-viewY
      areaWidth: null, // movable-area
      areaHeight: null,// movable-area
      viewWidth: null, // movable-view
      viewHeight: null,// movable-view
      maxX: null, // x
      maxY: null, // y
      resilience: false, // 
      startDistance: null,     // 
      currentDistance: null,    // 
      multipleFinger: false, // 2
      canUseAnimation: true     // propanimation
    }
  },

  watch: {
    x: {
      handler: function (newVal, oldVal) {
        this.mX = newVal
        this.calcPosition()
      }
    },
    y: {
      handler: function (newVal, oldVal) {
        this.mY = newVal
        this.calcPosition()
      }
    }
  },

  computed: {
    transitionDuration() {
      let transitionDurationMs = ''
      this.damping > 0 ? transitionDurationMs = (-25) * this.damping + 1500 + 'ms' : transitionDurationMs = '0ms'
      return transitionDurationMs
    },
    transitionTimingFunction() {
      return this.inertia ? `cubic-bezier(.165, .84, .44, ${this.resilience ? 1.25 : 1})` : 'linear'
    }
  },

  methods: {
    /**
     * movable-view
     */
    calcPosition() {
      if (this.outOfBounds) {
        this.setDirection(this.direction, {
          currentX: this.mX, currentY: this.mY
        })
        // this.updateBoxPos(this.mX, this.mY)
        return
      }
      const viewWidth = Number(this.view.style.width.substring(0, this.view.style.width.length - 2))
      const viewHeight = Number(this.view.style.height.substring(0, this.view.style.height.length - 2))
      this.maxX = this.areaWidth - viewWidth
      this.maxY = this.areaHeight - viewHeight
      if (viewWidth <= this.areaWidth && viewHeight <= this.areaHeight) {
        if (this.mX >= this.maxX) {
          this.mX = this.maxX
        } else if (this.mX < 0) {
          this.mX = 0

        }
        if (this.mY >= this.maxY) {
          this.mY = this.maxY

        } else if (this.mY < 0) {
          this.mY = 0

        }

      } else {
        if (viewWidth > this.areaWidth && viewHeight <= this.areaHeight) {
          if (this.mX <= this.maxX) {
            this.mX = this.maxX
          } else if (this.mX > 0) {
            this.mX = 0
          }
          if (this.mY > this.maxY) {
            this.mY = this.maxY
          } else if (this.mY < 0) {
            this.mY = 0
          }
        }
        if (viewWidth <= this.areaWidth && viewHeight > this.areaHeight) {
          if (this.mX >= this.maxX) {
            this.mX = this.maxX
          } else if (this.mX < 0) {
            this.mX = 0
          }
          if (this.mY <= this.maxY) {
            this.mY = this.maxY
          } else if (this.mY > 0) {
            this.mY = 0
          }
        }
        if (viewWidth > this.areaWidth && viewHeight > this.areaHeight) {
          if (this.mX <= this.maxX) {
            this.mX = this.maxX
          } else if (this.mX > 0) {
            this.mX = 0
          }
          if (this.mY <= this.maxY) {
            this.mY = this.maxY
          } else if (this.mY > 0) {
            this.mY = 0
          }
        }
      }
      // this.updateBoxPos(this.mX, this.mY)
      this.setDirection(this.direction, {
        currentX: this.mX, currentY: this.mY
      })
    },
    /**
     * movable-view
     * @param {Number} currentX x
     * @param {Number} currentY y
     */
    updateBoxPos(currentX, currentY) {
      // 
      if (this.inertia) {
        // x y =>  =>  => 
        // 
        if (currentX === this.maxX || currentY === this.maxY) {
          this.resilience = true
        } else {
          this.resilience = false
        }
      } // 
      this.view.style.left = currentX + 'px'
      this.view.style.top = currentY + 'px'
    },
    /**
     * movable
     * @param {String} direction 
     * @param {Object} curPos 
     */
    setDirection(direction, curPos) {
      if (direction === 'all') {
        this.updateBoxPos(curPos.currentX, curPos.currentY)
      } else if (direction === 'vertical') {
        this.updateBoxPos(this.boxX, curPos.currentY)
      } else if (direction === 'horizontal') {
        this.updateBoxPos(curPos.currentX, this.boxY)
      } else { // 'none'
        this.updateBoxPos(this.boxX, this.boxY)
      }
    },

    /**
     * 
     * @param {Event} e
     */
    getTwoFingersDistance(e) {
      let finger1 = e.targetTouches[0]  // touch
      let finger2 = e.targetTouches[1]  // touch
      let dx = Math.abs(finger1.pageX - finger2.pageX)
      let dy = Math.abs(finger1.pageY - finger2.pageY)
      return parseInt(Math.sqrt(Math.pow(dx, 2) + Math.pow(dy, 2)))
    },

    /**
     * 
     * @param {Event} e
     */
    touchstart(e) {
      // 
      this.startX = e.targetTouches[0].pageX
      this.startY = e.targetTouches[0].pageY
      // movable-viewmovable-area
      this.boxX = this.view.offsetLeft  // this.mX
      this.boxY = this.view.offsetTop// this.mY
      this.mX = this.view.offsetLeft
      this.mY = this.view.offsetTop
      if (e.targetTouches.length >= 2) {
        this.multipleFinger = true
        this.startDistance = this.getTwoFingersDistance(e)
      }
      this.$emit('change', {
        currentTarget: e.touches,
        detail: { x: this.mX, y: this.mY, source: e.type },
        target: e.target,
        timeStamp: e.timeStamp,
        type: 'change'
      })

    },
    /**
     * 
     * @param {Event} e
     */
    multipleFingerOperation(e) {
      this.currentDistance = this.getTwoFingersDistance(e)
      let scaleRatio = (this.currentDistance / this.startDistance).toFixed(2)
      if (scaleRatio <= this.scaleMin) {
        scaleRatio = this.scaleMin
      }
      if (scaleRatio >= this.scaleMax) {
        scaleRatio = this.scaleMax
      }
      this.view.style.transform = `scale(${scaleRatio})`
      this.mX = this.view.offsetLeft // 
      this.mY = this.view.offsetTop
      this.$emit('scale', {
        currentTarget: e.touches,
        detail: { x: this.mX, y: this.mY, scaleRatio },
        target: e.target,
        timeStamp: e.timeStamp,
        type: 'scale'
      })
    },

    /**
     * 
     * @param {Event} e
     */
    singleFingerOperation(e) {
      // 1
      // 


      let moveX = e.targetTouches[0].pageX - this.startX
      let moveY = e.targetTouches[0].pageY - this.startY
      //  +
      this.mX = this.boxX + moveX
      this.mY = this.boxY + moveY
      this.calcPosition()
      this.$emit('change', {
        currentTarget: e.touches,
        detail: { x: this.mX, y: this.mY, source: e.type },
        target: e.target,
        timeStamp: e.timeStamp,
        type: 'change'
      })
    },

    /**
     * 
     * @param {Event} e
     */
    touchmove(e) {
      // 
      e.preventDefault()

      // 
      this.canUseAnimation = false
      if (this.disabled) {
        return
      }
      if (this.scale && this.multipleFinger) {
        this.multipleFingerOperation(e)
      } else {
        this.singleFingerOperation(e)
      }
    },

    /**
     * 
     * @param {Event} e
     */
    touchend(e) {
      this.canUseAnimation = true
      if (this.scale && this.multipleFinger) {
        this.multipleFinger = false
        let oldWidth = this.view.offsetWidth
        let oldHeight = this.view.offsetHeight

        let scaleWidth = this.view.getBoundingClientRect().width
        let scaleHeight = this.view.getBoundingClientRect().height

        this.view.style.transform = `none`
        this.view.style.width = parseInt(scaleWidth) + 'px'
        this.view.style.height = parseInt(scaleHeight) + 'px'

        let offsetX = (scaleWidth - oldWidth) / 2
        let offsetY = (scaleHeight - oldHeight) / 2
        // 
        this.view.style.left = parseInt((this.view.offsetLeft - offsetX)) + 'px'
        this.view.style.top = parseInt((this.view.offsetTop - offsetY)) + 'px'

      }

      this.$emit('change', {
        currentTarget: e.touches,
        detail: { x: this.mX, y: this.mY, source: e.type },
        target: e.target,
        timeStamp: e.timeStamp,
        type: 'change'
      })
    }
  },

  created() {
    this.mX = this.x ? this.x : 0
    this.mY = this.y ? this.y : 0
  },

  mounted() {
    this.view = this.$refs.movableView
    this.areaView = this.$refs.movableView.offsetParent
    this.areaWidth = this.areaView.offsetWidth
    this.areaHeight = this.areaView.offsetHeight
    this.viewWidth = this.view.offsetWidth
    this.viewHeight = this.view.offsetHeight
    this.maxX = this.areaView.offsetWidth - this.view.offsetWidth
    this.maxY = this.areaView.offsetHeight - this.view.offsetHeight
    this.boxX = this.mX  // this.mX
    this.boxY = this.mY// this.mY
    // 
    this.calcPosition()
    if (this.scaleValue <= this.scaleMin) { this.scaleValue = this.scaleMin }
    if (this.scaleValue >= this.scaleMax) { this.scaleValue = this.scaleMax }
    this.view.style.transform = `scale(${this.scaleValue})`
  },

  render(h) {
    return h('wx-movable-view', {
      ref: 'movableView',
      style: {
        transition: this.animation && this.canUseAnimation ? `all ${this.transitionDuration} ${this.transitionTimingFunction}` : 'none'
      },
      on: {
        touchstart: this.touchstart,
        touchmove: this.touchmove,
        touchend: this.touchend
      }
    }, this.$slots.default)
  },
}
Vue.component('x-movable-view', MovableView)
